export default (windSpeed) => {
  let direction
  if (windSpeed[0] === 0) {
    if (windSpeed[1] >= 0) {
      direction = ''
    } else {
      direction = ''
    }
  } else {
    if (windSpeed[0] > 0 && windSpeed[1] > 0) {
      direction = ''
    } else if (windSpeed[0] > 0 && windSpeed[1] < 0) {
      direction = ''
    } else if (windSpeed[0] < 0 && windSpeed[1] > 0) {
      direction = ''
    } else if (windSpeed[0] < 0 && windSpeed[1] < 0) {
      direction = ''
    } else if (windSpeed[0] > 0 && windSpeed[1] === 0) {
      direction = ''
    } else if (windSpeed[0] < 0 && windSpeed[1] === 0) {
      direction = ''
    }
  }
  return direction
}
